.main-header {
    display: block;
    width: 100%;
    border-bottom: 1px solid #ddd;

    z-index: 99;

    transition: $effect-time;

    &--moved {
        background: rgba(0, 0, 0, .702);

        .main-header__logo {
            font-size: $fontXMd;
            line-height: $fontXMd; } }

    &__wrap {
        @include wrap;
        @include flex;
        max-width: 960px;

        @media #{$tablet} {
            padding: 0 15px; } } }

