.main-screen {
    display: block;
    position: relative;

    background: url(../img/main-screen/bg.jpg) center fixed;
    background-size: cover;

    &::before {
        @include before_darken; }

    &--slider {
        position: relative; }

    &__wrap {
        @include wrap;

        padding: 200px 0;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        text-align: center;

        z-index: 1;

        &--pos {
            &_left {
                align-items: flex-start;
                text-align: left; }

            &_left-bottom {
                align-items: flex-start;
                justify-content: flex-end;
                text-align: left; }

            &_right-bottom {
                align-items: flex-end;
                justify-content: flex-end;
                text-align: right; } }

        &--form {
            flex-direction: row;
            text-align: left; } }

    &__header {
        @include page_header;
        font-weight: 700;
        color: $font-secondary-color;

        small {
            color: $font-secondary-color; } }

    &__actions {
        display: flex;
        width: 50%;
        justify-content: center;
        margin-bottom: 3em; }

    &__btn {
        margin-right: 1em;

        &:last-child {
            margin-right: 0; } }

    &__input {
        margin-right: 1em;
        padding: 0 .5em;
        flex-grow: 1; } }

