  background: url("../img/contacts-section/bg-headphones.jpg") 0 3%, url("../img/contacts-section/bg-glasses.jpg") 45% 55%, url("../img/contacts-section/bg-clips.jpg") 2% 97%, url("../img/contacts-section/bg-pencil.jpg") right 95%, url("../img/contacts-section/bg-pen-holder.jpg") right 3%;
  background-repeat: no-repeat; }
  @media screen and (max-width: 600px) {
    .contacts-section {
      background: url("../img/contacts-section/bg-pencil.jpg") right 95%, url("../img/contacts-section/bg-pen-holder.jpg") 125% 3%;
      background-repeat: no-repeat;
      padding: 100px 0 80px; } }
  .contacts-section__wrap {
    display: block;
    position: relative;
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-items: flex-start;
    flex-wrap: wrap; }
    @media screen and (max-width: 960px) and (min-width: 601px) {
      .contacts-section__wrap {
        flex-wrap: wrap; } }
  .contacts-section__header {
    display: inline-block;
    position: relative;
    padding: 30px 0;
    margin: 0 0 60px;
    font-size: 30px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #212121;
    font-weight: bold;
    border-bottom: 1px solid #BDBDBD;
    font-size: 25px;
    letter-spacing: 1px;
    min-width: 100%; }
    .contacts-section__header::before {
      content: "";
      display: inline-block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 50px;
      height: 3px;
      background: #FFC107; }
    @media screen and (max-width: 600px) {
      .contacts-section__header {
        font-size: 25px;
        letter-spacing: 1px;
        margin-bottom: 40px; } }
  .contacts-section__left, .contacts-section__right {
    display: block;
    box-sizing: border-box; }
  .contacts-section__left {
    width: 400px; }
    @media screen and (max-width: 960px) and (min-width: 601px) {
      .contacts-section__left {
        width: 100%;
        margin-bottom: 30px; } }
  .contacts-section__right {
    width: 570px; }
    @media screen and (max-width: 960px) and (min-width: 601px) {
      .contacts-section__right {
        width: 100%; } }
  @media screen and (max-width: 960px) and (min-width: 601px) {
    .contacts-section__list {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      align-items: flex-start; } }
  @media screen and (max-width: 960px) and (min-width: 601px) and (max-width: 600px) {
    .contacts-section__list {
      flex-wrap: wrap; } }
  .contacts-section__item {
    display: flex;
    margin-bottom: 55px; }
    @media screen and (max-width: 960px) and (min-width: 601px) {
      .contacts-section__item {
        width: 50%;
        margin-bottom: 0; } }
    @media screen and (max-width: 600px) {
      .contacts-section__item {
        width: 100%;
        margin-bottom: 30px; }
        .contacts-section__item:last-of-type {
          margin-bottom: 0; } }
  .contacts-section__img {
    display: block;
    position: relative;
    height: 60px;
    margin-right: 20px;
    background-image: url("../img/contacts-section/icons/icon-bg.png");
    background-size: cover; }
    .contacts-section__img img {
      display: inline-block;
      position: relative;
      top: 50%;
      left: 45%;
      transform: translate(-50%, -50%); }
  .contacts-section__info {
    display: block;
    font-size: 16px; }
    .contacts-section__info header {
      display: block;
      text-transform: uppercase;
      font-weight: 700;
      letter-spacing: 1px;
      margin-bottom: 15px; }
    .contacts-section__info p, .contacts-section__info a {
      color: #757575;
      font-size: 14px; }
  .contacts-section__link {
    margin-left: 5px; }
    .contacts-section__link:first-of-type {
      margin-left: 0; }

.wrap {
  display: block;
  position: relative;
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto; }
