.result {
    &__wrap {
        @include wrap; }

    &__header {
        @include section_header; }

    &__work {}

    &__works {}

    &__work-current {
        display: none;

        &--active {
            display: flex;
            justify-content: space-between; } }

    &__work-img {
        min-width: 60%;

        img {
            width: 100%; } }

    &__work-descr {
        margin-left: 20px; }

    &__work-text {}

    &__work-link {}

    &__slides {
        display: flex;
        justify-content: center;
        margin-left: -10px;
        margin-right: -10px;

        &-item {
            margin-left: 10px;
            margin-right: 10px;
            img {
                width: 100%; } } } }
