.main-nav {
    &--moved {
        .main-nav__link {
            padding: 17px 5px; } }

    &__list {
        @media #{$mobile} {
            display: none; } }

    &__item {
        display: inline-block;
        margin-right: 15px;

        &:last-of-type {
            margin-right: 0; } }

    &__link {
        display: block;
        padding: 20px 5px;

        font-size: $fontXSm - 1px;
        text-transform: uppercase;
        letter-spacing: 2px;
        color: $font-color;

        transition: $effect-time;

        &--active {
            color: $font-color; }

        &:hover {
            color: $akcent-color; }

        @media #{$tablet} {
            font-size: $fontMicro; } } }
