// Colors
$font-color: #212121;
$font-secondary-color: #fff;
$font-par-color: #757575;

$font-footer-color: rgb(153, 153, 153);
$font-footer-secondary-color: #dddddd;

$akcent-color: #FFC107;
$secondary-color: #fff;
$special-color: #3F51B5;
$border-color: #BDBDBD;

$input-border: #C5CAE9;
$footer-bottom-bg: #000000;
$footer-top-bg: #1a1a1a;

// Fonts
//=====================================================
$font-link: 'https://fonts.googleapis.com/css?family=Open+Sans:300,400,400i,700&subset=cyrillic';
$default-font: 'Open Sans', sans-serif;
$secondary-font: 'Raleway', sans-serif;

$fontEXLg: 70px; // extra large
$fontXLg: 55px;
$fontLg: 40px;
$fontXBig: 30px;
$fontBig: 25px;
$fontXMd: 22px;
$fontBigMd: 20px;
$fontMd: 18px;
$fontSm: 16px;
$fontXSm: 14px;
$fontMicro: 12px;

// Dimensions
//=====================================================
$width-min: 320px;
$width-primary: 1440px;
$width-inner: 1180px;
$height-primary: 600px;

$mobileSm: "screen and (max-width: 380px)";
$mobile: "screen and (max-width: 600px)";
$tablet: "screen and (max-width: 960px) and (min-width: 601px)";
$desktop: "screen and (max-width: 1280px)";

// Mix
//=====================================================
$effect-time: .4s;
